import React, { Component } from "react";
import "./landingpage.css";
import Footer from "../../components/footer/footer";

import Typerwriter from "../../components/typewriter/typewriter";

class Landingpage extends Component {
  state = {};

  componentDidMount() {}

  btn1handler = e => {};

  render() {
    return (
      <div className="parallax">
        <main>
          <div className="parallaxTextdiv">
            <Typerwriter
              toRotate={[
                "ආයුබෝවන්         ",
                "Hello              ",
                "Ciao               ",
                "Hola               ",
                "Здравствуйте       ",
                "こんにちは          ",
                "Hallo              ",
                "Bonjour            ",
                "नमस्कार             ",
                "Aloha              "
              ]}
              period={8900}
            />
            <br />
            <Typerwriter
              toRotate={[
                "ඔබට කොහොමද    ",
                "How you doing       ",
                "Come va             ",
                "Como estas          ",
                "Как дела            ",
                "元気かい             ",
                "Hvordan går det     ",
                "Comment vas-tu      ",
                "आप कैसे है           ",
                "Pehea 'oe e hana nei"
              ]}
              period={8900}
            />
            <br />
            <Typerwriter
              toRotate={[
                "මම රජිත            ",
                "I'am Rajitha         ",
                "Sono Rajitha         ",
                "Soy Rajitha          ",
                "Я Раджита            ",
                "私はラジタです        ",
                "Jeg er Rajitha       ",
                "Je suis Rajitha      ",
                "आयम राजतिथा          ",
                "Iʻii Rajitha          "
              ]}
              period={8900}
            />

            {/* <br />
            <span className="parallaxText">How you doing </span>
            <br />
            <span className="parallaxText">I'am Rajitha </span> */}
            <br />
            <button
              className="btn visitblogbtn btn-outline-primary "
              onClick={() => this.props.history.push("/blog")}
            >
              visit my blog
            </button>
          </div>
        </main>
        <Footer />
      </div>
    );
  }
}

export default Landingpage;
